import productLikesReducer from '../common/features/Like/productlikesreducer'
import cartReducer from '../common/features/Cart/cartReducer'


const rootReducer = (state={}, action) => {
    return {
        likeProducts: productLikesReducer(state.likeProducts, action),
        productsInCart: cartReducer(state.productsInCart, action)
    }
}

export default rootReducer